.filter-wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  .filter-left {
    width: 35%;
    .filter-in {
      padding: 25px;
      border: 1px solid $color6;
      border-radius: 12px;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
    .form-style {
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    .search-wrapper {
      width: 100%;
    }
    .styled-btn {
      width: 100%;
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    .f-item {
      &.age {
        .polzunok-container-5 * {
          box-sizing: border-box;
          outline: none;
        }
        .polzunok-container-5 {
          padding: 10px 16px;
          margin: 30px;
        }
        .polzunok-container-5:after {
          content: "";
          clear: both;
          display: table;
        }
        .polzunok-container-5 .ui-slider {
          position: relative;
          background: #BFE2FF;
          height: 10px;
          border-radius:10px;
          box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.2), 1px 1px 5px rgba(255, 255, 255, 1);
        }
        .polzunok-container-5 .ui-slider .ui-slider-handle {
          position: absolute;
          margin-left: -16px;
          width: 32px;
          height: 32px;
          cursor: pointer;
          border-radius: 50%;
          border: 4px solid #BFE2FF;
          top: -11px;
          background: #FFF;
          z-index: 2;
          box-shadow: 0 2px 5px rgba(0,0,0,0.2), 0 4px 6px rgba(0,0,0,0.2);
          transition: transform 0.2s, border 0.4s;
        }
        .polzunok-container-5 .ui-slider .ui-slider-handle:hover,
        .polzunok-container-5 .ui-slider .ui-slider-handle:focus {
          transform: scale(1.2);
          border: 4px solid #337AB7;
        }
        .polzunok-input-5-left,
        .polzunok-input-5-right {
          height: 32px;
          background: #FFF;
          border-radius: 28px;
          border: 4px solid #BFE2FF;
          text-align:center;
          font-size: 18px;
          padding: 16px;
          width: 140px;
          margin-top:30px;
          box-shadow: 0 2px 5px rgba(0,0,0,0.2), 0 4px 6px rgba(0,0,0,0.2);
        }
        .polzunok-input-5-left {
          float: left;
        }
        .polzunok-input-5-right {
          float: right;
        }
        .polzunok-container-5 .ui-slider .ui-slider-range {
          position: absolute;
          z-index: 1;
          height: 10px;
          border: 0;
          background: #337AB7;
          border-radius:10px;
          box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.3), 1px 1px 5px rgba(255, 255, 255, 1);
        }
      }
    }
  }
  .filter-right {
    width: calc(65% - 40px);
  }
}